*,*:after,*:before{
    box-sizing: border-box;
}

body{
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 100vh;
    background: #f8f9fa;
}



.circle{
    padding:0;
    margin:0;
    position: relative;
    outline: 1px solid red;
    svg{
        width: 200px;
        height: 200px;    
        transform: rotate(-90deg);
        overflow: initial;
        margin: 0;
    } 

    .svg{
        stroke-width:10px;
        fill:none;
        stroke-dashoffset:440;
        stroke-dasharray:440;
// back here    

}
}

.svg__inner{
    stroke: #5247eb; 
    stroke-dashoffset:0;
    
   }

.svg__outer{
    stroke: #ddd7d7;
    position: relative;
    stroke-dashoffset:0;
    z-index: 1;
    //stroke-dashoffset:calc(440 - ( 440 * 120 )/120); 
}

.timmer{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 2rem;
    font-weight: bold;
    color: #2849da;
}

.btn{
    margin:0.4rem 0.1rem;
    padding:0 1rem;
    color:white;
    background:#2849da;
}